import axios from 'axios';
// import store from 'src/store';
import {Message} from 'iview';
import {Modal} from 'iview';
import router from '../../routes'
//
import qs from 'qs'
// axios
const fetch = axios.create({

  baseURL: process.env.NODE_ENV === 'production' ? '' : "car",  // baseURL: 'http://localhost/', // apibase_url
  timeout: 60 * 1000                  // 
});


// request
fetch.interceptors.request.use(config => {
  // Do something before request is sent

  // config.baseURL = 'http://sdf/'
  // config.url = config.baseURL + config.url
  // console.log(store.getters.serverUrl)
  // console.log(baseUrl[store.getters.serverUrl])

  // config.url = 'dsf/d'
  // console.log(config.url)
  // if (store.getters.token) {
  config.headers['Authorization'] = sessionStorage.getItem("Authorization"); // token--['X-Token']key 
  // }
  return config;
}, error => {
  // Do something with request error
  console.error(error); // for debug
  Promise.reject(error);
})

// respone
fetch.interceptors.response.use(
  response => {
    sessionStorage.setItem("Authorization", response.headers.authorization)
    return response
  }

  /**
   * responsecodecode
   * xmlhttprequest  error
   */
  // const code = response.data.code;
  // // 50014:Token  50012: 50008:token
  // if (code === 50008 || code === 50014 || code === 50012) {
  //   Message({
  //     message: res.message,
  //     type: 'error',
  //     duration: 5 * 1000
  //   });
  //   // 
  //   store.dispatch('FedLogOut').then(() => {
  //     router.push({ path: '/login' })
  //   });
  // } else {
  //   return response
  // }
  ,
  error => {
    if (error.response.status === 401) {
      router.replace({path: '/login', query: {redirect: router.currentRoute.fullPath}})
      Message.error("", 3);
      Modal.remove()
      return Promise.reject("");
    }
    Message.error('err' + error);
    return Promise.reject(error);
  }
)

export {fetch}
export {qs}



